import { tsvParse, csvParse } from "d3-dsv";
import { timeParse } from "d3-time-format";
var sleep = require('sleep');
function convertData(data) {
    var keys = ["date", "open", "close", "high", "low", "volume"],
      i = 0,
      k = 0,
      obj = null,
      output = [];
    for (i = 0; i < data.length; i++) {
      obj = {};
      obj[keys[0]] = new Date(data[i].openTime);
      obj[keys[1]] = data[i].open;
      obj[keys[2]] = data[i].close;
      obj[keys[3]] = data[i].high;
      obj[keys[4]] = data[i].low;
      obj[keys[5]] = data[i].volume;

      output.push(obj);
    }
    return output;
  }

const parseDate = timeParse("%Y-%m-%d");
export function getData() {
  const promiseData = fetch("/api/chart")
    .then(response => response.json())
		.then(data => convertData(data))
    .then(data => data.reverse())
  
  return promiseData;
}


